import {
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  Paper,
  Typography,
} from "@mui/material";
import KeyboardReturnIcon from "@mui/icons-material/KeyboardReturn";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import UndoIcon from "@mui/icons-material/Undo";
import RedoIcon from "@mui/icons-material/Redo";
import { Stack } from "@mui/system";

function History({ history, view, viewHistory }) {
  const undo = () => {
    viewHistory(view + 1);
  };

  const redo = () => {
    viewHistory(view - 1);
  };

  return (
    <Paper variant="outlined" sx={{ p: 2 }}>
      <Stack direction="row" gap={2}>
        <IconButton
          size="small"
          onClick={undo}
          disabled={view === history.length - 1}
        >
          <UndoIcon fontSize="small" />
        </IconButton>
        <Typography variant="h6">History</Typography>
        <IconButton
          size="small"
          onClick={redo}
          disabled={view === -1 || view === 0}
        >
          <RedoIcon fontSize="small" />
        </IconButton>
      </Stack>
      <List
        sx={{
          overflow: "auto",
          maxHeight: 300,
        }}
      >
        {history.map((state, index) => (
          <ListItem
            key={index}
            disablePadding
            secondaryAction={
              index === view ? (
                <IconButton size="small" disabled>
                  <RemoveRedEyeIcon fontSize="small" />
                </IconButton>
              ) : (
                <IconButton size="small" disableRipple>
                  <KeyboardReturnIcon fontSize="small" />
                </IconButton>
              )
            }
          >
            <ListItemButton
              onClick={() => viewHistory(index)}
              selected={index === view}
            >
              <ListItemIcon>
                <span
                  className={`reversi-piece reversi-${state.action.player}-piece reversi-tiny`}
                />
              </ListItemIcon>
              <Typography key={index} variant="subtitle">
                {state.action.row}, {state.action.col}
              </Typography>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Paper>
  );
}

export default History;
