import {
  Button,
  Checkbox,
  IconButton,
  Menu,
  MenuItem,
  Stack,
} from "@mui/material";
import { findValidMoves } from "./rule";
import SettingsIcon from "@mui/icons-material/Settings";
import { useState } from "react";

function ControlPanel({
  newGame,
  aiMove,
  history,
  gameState,
  openAdvance,
  toogleAdvance,
}) {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleAdvanceSetting = () => {
    toogleAdvance();
    setAnchorEl(null);
  };

  return (
    <Stack flexDirection="row" gap={2} sx={{ mt: 2, mb: 1 }}>
      <Button
        onClick={newGame}
        variant="outlined"
        disabled={history.length === 0}
      >
        New Game
      </Button>
      <Button
        onClick={aiMove}
        variant="outlined"
        color="secondary"
        disabled={
          findValidMoves(gameState.board, gameState.player).length === 0
        }
      >
        AI play
      </Button>
      <IconButton onClick={(e) => setAnchorEl(e.target)}>
        <SettingsIcon />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={() => setAnchorEl(null)}
      >
        <MenuItem onClick={handleAdvanceSetting}>
          Advance Setting <Checkbox disabled checked={openAdvance} />
        </MenuItem>
      </Menu>
    </Stack>
  );
}

export default ControlPanel;
