import React from "react";
import Square from "./Square";

const GameBoard = ({ board, validBoard, handleClick }) => {
  return (
    <div className="board">
      {board.map((row, rowIndex) => (
        <div key={rowIndex}>
          {row.map((square, colIndex) => (
            <Square
              key={colIndex}
              piece={square}
              handleClick={() => handleClick(rowIndex, colIndex)}
              isValidMove={validBoard[rowIndex][colIndex]}
            />
          ))}
        </div>
      ))}
    </div>
  );
};

export default GameBoard;
