import { isGameOver, findValidMoves, updateBoard, countPieces } from "./rule";

function evaluate(board) {
  const { black, white } = countPieces(board);
  return black - white;
}

function alphaBeta(board, depth, alpha, beta, maximizingPlayer) {
  let possibleMoves = null;
  if (maximizingPlayer) possibleMoves = findValidMoves(board, "black");
  else possibleMoves = findValidMoves(board, "white");

  if (depth === 0 || isGameOver(board, possibleMoves)) {
    const score = evaluate(board);
    return score;
  }

  if (maximizingPlayer) {
    let maxScore = -Infinity;
    for (let move of possibleMoves) {
      const newBoard = updateBoard(board, "black", move[0], move[1]);
      const score = alphaBeta(newBoard, depth - 1, alpha, beta, false);
      maxScore = Math.max(maxScore, score);
      alpha = Math.max(alpha, score);
      if (beta <= alpha) {
        break;
      }
    }
    return maxScore;
  } else {
    let minScore = Infinity;
    for (let move of possibleMoves) {
      const newBoard = updateBoard(board, "white", move[0], move[1]);
      const score = alphaBeta(newBoard, depth - 1, alpha, beta, true);
      minScore = Math.min(minScore, score);
      beta = Math.min(beta, score);
      if (beta <= alpha) {
        break;
      }
    }
    return minScore;
  }
}

function getBestMove(board, player, depth) {
  const maximize = player === "black";
  const possibleMoves = findValidMoves(board, player);
  let bestMove = null;

  if (maximize) {
    let maxScore = -Infinity;
    possibleMoves.forEach((move) => {
      const newBoard = updateBoard(board, "black", move[0], move[1]);
      const score = alphaBeta(newBoard, depth - 1, maxScore, Infinity, false);
      if (score > maxScore) {
        bestMove = move;
        maxScore = score;
      }
    });
    return [bestMove, maxScore];
  } else {
    let minScore = Infinity;
    possibleMoves.forEach((move) => {
      const newBoard = updateBoard(board, "white", move[0], move[1]);
      const score = alphaBeta(newBoard, depth - 1, -Infinity, minScore, true);
      if (score < minScore) {
        bestMove = move;
        minScore = score;
      }
    });
    return [bestMove, minScore];
  }
}

export { getBestMove };
