import { Box, Grid } from "@mui/material";
import { useEffect, useState } from "react";
import { getBestMove } from "./ai";
import GameBoard from "./GameBoard";
import Header from "./Header";
import History from "./History";
import AdvancePanel from "./AdvancePanel";
import {
  countPieces,
  DEFAULT_BOARD,
  DEFAULT_START_PLAYER,
  DEFAULT_VALID_BOARD,
  DEFAULT_PIEACES,
  findValidMoves,
  updateBoard,
  getValidBoard,
} from "./rule";
import ControlPanel from "./ControlPanel";

function Reversi() {
  const [gameState, setGameState] = useState({
    board: DEFAULT_BOARD,
    player: DEFAULT_START_PLAYER,
  });
  const [validBoard, setValidBoard] = useState(DEFAULT_VALID_BOARD);
  const [piecesNumber, setPiecesNumber] = useState(DEFAULT_PIEACES);
  const [history, setHistory] = useState([]);
  const [view, setView] = useState(-1);
  const [predict, setPredict] = useState(null);
  const [treeDeepth, setTreeDeepth] = useState(8);
  const [openAdvance, setOpenAdvance] = useState(false);

  const handleClick = (row, col, isAI) => {
    const empty = Boolean(!gameState.board[row][col]);
    if (empty && validBoard[row][col]) {
      const newBoard = updateBoard(gameState.board, gameState.player, row, col);
      const nextPlayer = gameState.player === "black" ? "white" : "black";
      const newGameState = { board: newBoard, player: nextPlayer };
      if (!isAI) setPredict(null);
      updateHistory(newGameState, gameState.player, row, col);
      setGameState(newGameState);
    }
  };

  const newGame = () => {
    setGameState({
      board: DEFAULT_BOARD,
      player: DEFAULT_START_PLAYER,
    });
    setHistory([]);
    setView(-1);
  };

  const aiMove = () => {
    const start = new Date();
    const [move, score] = getBestMove(
      gameState.board,
      gameState.player,
      treeDeepth
    );
    const time = new Date() - start;
    setPredict({ score, treeDeepth, time });
    handleClick(move[0], move[1], true);
  };

  const updateHistory = (gameState, player, row, col) => {
    const action = { player, row, col };
    const record = { gameState, action };
    if (view === -1) setHistory([record]);
    else setHistory([record, ...history.slice(view)]);
    setView(0);
  };

  const viewHistory = (index) => {
    setView(index);
    setGameState(history[index].gameState);
  };

  const handleDeepthChange = (value) => {
    setTreeDeepth(value);
  };

  const toogleAdvance = () => setOpenAdvance((prev) => !prev);

  useEffect(() => {
    const validMoves = findValidMoves(gameState.board, gameState.player);
    setValidBoard(getValidBoard(validMoves));
    setPiecesNumber(countPieces(gameState.board));
  }, [gameState]);

  return (
    <Box p={2} display="flex" alignItems="center" flexDirection="column">
      <Header player={gameState.player} piecesNumber={piecesNumber} />
      <Grid container maxWidth="sm" mt={2}>
        <Grid
          item
          xs={12}
          md={8}
          display="flex"
          flexDirection="column"
          alignItems="center"
        >
          <GameBoard
            board={gameState.board}
            validBoard={validBoard}
            handleClick={handleClick}
          />
          <ControlPanel
            newGame={newGame}
            aiMove={aiMove}
            openAdvance={openAdvance}
            toogleAdvance={toogleAdvance}
            history={history}
            gameState={gameState}
          />
          {openAdvance && (
            <AdvancePanel
              predict={predict}
              treeDeepth={treeDeepth}
              handleDeepthChange={handleDeepthChange}
            />
          )}
        </Grid>
        <Grid item xs={12} md={4}>
          <History history={history} view={view} viewHistory={viewHistory} />
        </Grid>
      </Grid>
    </Box>
  );
}

export default Reversi;
