import React from "react";

const Square = ({ piece, handleClick, isValidMove }) => {
  return (
    <div
      className={`reversi-square ${isValidMove ? "reversi-valid-move" : ""}`}
      onClick={handleClick}
    >
      {piece && <div className={`reversi-piece reversi-${piece}`} />}
    </div>
  );
};

export default Square;
