import { Typography } from "@mui/material";
import { Stack } from "@mui/system";

function Header({ player, piecesNumber }) {
  return (
    <>
      <Stack flexDirection="row" gap={10} mb={1}>
        <div className="reversi-piece reversi-black-piece">
          <Typography color="white" variant="h6">
            {piecesNumber.black}
          </Typography>
        </div>
        <Stack flexDirection="row">
          <Typography variant="h5">Turns:</Typography>
          <span
            className={`reversi-piece reversi-${player}-piece reversi-small`}
          />
        </Stack>
        <div className="reversi-piece reversi-white-piece">
          <Typography variant="h6">{piecesNumber.white}</Typography>
        </div>
      </Stack>
    </>
  );
}

export default Header;
