import { Divider, Paper, Slider, Typography } from "@mui/material";

const minDeepth = 1;
const maxDeepth = 15;

function InfoPanel({ predict, treeDeepth, handleDeepthChange }) {
  return (
    <Paper variant="outlined" sx={{ width: 350, p: 1.5, mt: 1, mb: 1 }}>
      {predict !== null && predict.score === 0 && (
        <Typography>Prediciton: Draw</Typography>
      )}
      {predict !== null && predict.score !== 0 && (
        <Typography
          sx={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          Prediciton:
          <span
            className={`reversi-tiny reversi-piece reversi-${
              predict.score > 0 ? "black" : "white"
            }-piece`}
          />
          win {Math.abs(predict.score)} pieces after {predict.treeDeepth} moves
        </Typography>
      )}
      {predict !== null && (
        <>
          <Typography>Running time: {predict.time / 1000} s</Typography>
          <Divider sx={{ mt: 1 }} />
        </>
      )}
      <Typography mt={1}>AI Predict Moves:</Typography>
      <Slider
        valueLabelDisplay="off"
        min={minDeepth}
        max={maxDeepth}
        marks={[{ label: treeDeepth, value: treeDeepth }]}
        value={treeDeepth}
        onChange={(e) => handleDeepthChange(e.target.value)}
      ></Slider>
    </Paper>
  );
}

export default InfoPanel;
